import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"
import Layout from "../components/Layout"
import styled from "styled-components"
import { StaticImage } from "gatsby-plugin-image"
import { BiLogoJavascript, BiLogoTypescript} from "react-icons/bi";
import { FaGitAlt } from "react-icons/fa6";
import { SiDjango, SiVite, SiPrisma } from "react-icons/si";
import { TbBrandMongodb } from "react-icons/tb";
import { RiNextjsFill } from "react-icons/ri";
import { FaGithub, FaGitlab, FaFigma, FaNodeJs, FaReact, FaPython, FaDocker } from "react-icons/fa";

import { FiMail } from "react-icons/fi";
import { FiPhone } from "react-icons/fi";
import { FiLinkedin } from "react-icons/fi";
import { FiGithub } from "react-icons/fi";
import { FiDownload } from "react-icons/fi";

const StyledWrapper = styled.main`
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  height: 100vh;

  @media (max-width: 1000px) {
    flex-direction: column;
  }

`

const StyledLeft = styled.article`
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: center;
  min-height: 100dvh;

  h1{
    font-weight: bold;
    font-size: 30px;
  }

  h2{
    font-weight: 300;
    font-size: 17px;
  }

  img{
    width: 320px;
  }
`

const StyledRight = styled.article`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 20px;
  min-height: 100dvh;

  a{
    display: flex;
    align-items: center;
    gap: 5px;
    color: black;
    text-decoration: none;
    transition: transform 0.2s, color 0.2s;

    &:hover{
      transform: translateX(10px);
      color: grey;
    }

  }
`

const StyledIconGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 20%);
  justify-content: center;
  gap: 13px;
  font-size: 22px;
  span{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.2s;

    &:hover{
      color: grey;
    }

  }

`

const IndexPage: React.FC<PageProps> = () => {
  return (
    <Layout>
      <StyledWrapper>
       <StyledLeft>
        <h1>Mateusz Zębala</h1>
        <h2>Junior Software Developer</h2>
        <StaticImage src="./me.jpeg" alt="Mateusz Zębala" width={320} height={320} draggable={false} style={{borderRadius: '21%'}} />
        <StyledIconGrid>
          <span title="Python"><FaPython/></span>
          <span title="JavaScript"><BiLogoJavascript/></span>
          <span title="TypeScript"><BiLogoTypescript/></span>
          <span title="React.js"><FaReact/></span>
          <span title="Git"><FaGitAlt/></span>
          <span title="Django"><SiDjango/></span>
          <span title="Node.js"><FaNodeJs/></span>
          <span title="Figma"><FaFigma/></span>
          <span title="Docker"><FaDocker /></span>
          <span title="MongoDB"><TbBrandMongodb/></span>
          <span title="Vite"><SiVite/></span>
          <span title="Next.js"><RiNextjsFill/></span>
          <span title="Prisma"><SiPrisma/></span>
          <span title="Github"><FaGithub/></span>
          <span title="Gitlab"><FaGitlab/></span>
        </StyledIconGrid>
       </StyledLeft>
       <StyledRight>
        <a target="_blank" href="mailto:mateusz.zebala.pl@gmail.com">
          <FiMail/> mateusz.zebala.pl@gmail.com
        </a>
        <a target="_blank" href="tel:+48662961751">
          <FiPhone/> +48 662 961 751
        </a>
        <a target="_blank" href="https://www.linkedin.com/in/mateusz-zebala/">
          <FiLinkedin/> Linkedin
        </a>
        <a target="_blank" href="https://github.com/mateuszzebala">
          <FiGithub/> Github
        </a>
        <a target="_blank" download={'MateuszZebala.pdf'} href="/MateuszZebala.pdf">
          <FiDownload/> Download CV
        </a>
       </StyledRight>
      </StyledWrapper>
    </Layout>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Mateusz Zębala - Junior Software Developer</title>
